.navigation {
  border-bottom: 1px solid lightgray; }
  .navigation a {
    color: #000; }
  .navigation__logo {
    border-right: 1px solid lightgray; }
  .navigation__items__lang {
    font-size: 11px;
    padding: 8px 8px; }
  .navigation__items__contact {
    border-bottom: 1px solid lightgray;
    padding: 3px 0; }
    .navigation__items__contact__item {
      font-size: 11px;
      padding: 1px 12px;
      border-right: 1px solid lightgray;
      color: #858585 !important; }
    .navigation__items__contact__item:first-child {
      padding-left: 0; }
    .navigation__items__contact__item:last-child {
      border-right: none; }
  .navigation__items__links__col {
    overflow: hidden; }
    .navigation__items__links__col ul {
      border-right: 1px solid lightgray; }
    .navigation__items__links__col li {
      padding: 0 2rem 0 0;
      text-transform: uppercase; }
  .navigation__items__links__info a {
    text-transform: uppercase; }
